<template lang="pug">
  .login-container
    .bg-wrapper
    .login-top 
      span.logo.iconfont.icon-qq1
      span.text QQ
    form.login-form.animated.fadeInUp 
      .form-item 
        input.form-input( type="text", placeholder="QQ号", v-model="account")  
      .form-item 
        input.form-input( type="password", placeholder="密码", v-model="password")  
      .form-item 
        button.submit-btn( @click.prevent="login") 登 录  
      .form-item.clearfix.login-help
        a.fr( @click.stop="$router.push({name: 'register'})") 新用户注册     
</template>
<script>
import { mapState } from 'vuex'
import { socketEmit } from '@/socket/socket'
export default {
  name: 'login',
  data() {
    return {
      account: '',
      password: ''
    }
  },
  methods: {
    login() {
      
      socketEmit('login', { account: this.account, password: this.password }).then(data => {
        localStorage.setItem('_token', data.data.token);
        this.$router.push({
          name: 'messages'
        })
      }).catch(err => {
        console.log(err, 'err')
      })
    }
  },
  created() {
    // 主动填充
    let registerModule = this.$store.state.register;
    if(registerModule) {
      this.account = registerModule.account || '';
      this.password = registerModule.password || '';
    }
  }
}
</script>
<style lang="stylus" scoped>
  .login-container 
    position relative
    box-sizing border-box
    height 100vh
    padding 0 1.6rem
    .bg-wrapper
      background-image url('../images/login4.jpg')
      background-repeat no-repeat 
      background-size 100% auto
      filter:blur(5px) 
      top 0 
      left 0 
      right 0
      bottom 0
      position absolute
      z-index -1
  .login-top
    color #ffffff
    line-height 2.25rem
    padding-top 5.725rem 
    .logo 
      font-size 2.25rem
    .text 
      font-size 1.6rem  
  .login-form 
    padding-top 2.75rem 
    .form-item 
      margin-bottom 1rem
    .form-input 
      background none
      outline none
      width 100%
      border none 
      border-bottom 1px solid rgba(255, 255, 255, .7)
      padding-bottom 1.1rem 
      font-size 1rem 
      color #ffffff 
    .submit-btn 
      width 100%
      background-color rgba(40, 174, 227, 0.7)
      padding .7rem 0
      font-size 1.2rem
      color #ffffff
      border none 
      outline none 
      border-radius 3px  
    .login-help 
      color #3fc7ef 
      a 
        color inherit            
</style>




